import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Button,
  Container,
  TextField,
  Typography,
  Paper,
  Grid,
} from '@mui/material';

const primaryColor = '#E04A39'; // Rood uit het logo
const textColor = '#232323'; // Donkergrijs uit het logo

const LoginPage: React.FC = () => {
  const navigate = useNavigate();
  const [loginMethod, setLoginMethod] = useState<'key' | 'code'>('key');
  const [input, setInput] = useState('');

  const newClients = [
    'Van den Broeck Bouw',
    'IT Solutions Gent',
    'De Smet Advies',
    'Bakkerij Van Damme',
    'Groep Janssens',
    'Eco Installaties',
    'Logistiek Wachtebeke',
    'Klusbedrijf Peeters',
  ];
  const [showNewClient, setShowNewClient] = useState(true);
  const [randomClient, setRandomClient] = useState(newClients[Math.floor(Math.random() * newClients.length)]);

  React.useEffect(() => {
    if (showNewClient) {
      const timer = setTimeout(() => {
        setShowNewClient(false);
      }, 6000);
      return () => clearTimeout(timer);
    }
  }, [showNewClient]);

  const handleLogin = () => {
    // In a real application, you would validate the key/code here
    if (input) {
      navigate('/portal');
    }
  };

  return (
    <Container component="main" maxWidth="xs">
      <Box
        sx={{
          marginTop: 8,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
        }}
      >
        {/* Pop-ups voor nieuwe diensten (links onderaan) */}
        <Box sx={{ position: 'fixed', bottom: 24, left: 24, display: 'flex', flexDirection: 'column', gap: 2, zIndex: 1300 }}>
          <Paper sx={{ p: 2, bgcolor: '#E04A39', color: '#fff', minWidth: 260, boxShadow: 6, borderRadius: 2, fontWeight: 700 }}>
            <Typography variant="subtitle1" sx={{ fontWeight: 700 }}>Nieuw: Bloxx View</Typography>
            <Typography variant="body2">
              Wij zetten jouw bedrijf in de kijker via social media campagnes, bedrijfsvideo's en websites. Een reclame boost zonder dure of langdurige contracten!
            </Typography>
          </Paper>
          <Paper sx={{ p: 2, bgcolor: '#232323', color: '#fff', minWidth: 260, boxShadow: 6, borderRadius: 2, fontWeight: 700 }}>
            <Typography variant="subtitle1" sx={{ fontWeight: 700 }}>Nieuw: Bloxx Deal</Typography>
            <Typography variant="body2">
              Bloxx brengt twee partijen samen en stelt een contract op, zodat beide partijen verzekerd zijn van inkomsten bij een juridisch geschil. Inclusief algemene voorwaarden voor 99 excl. btw.
            </Typography>
          </Paper>
        </Box>
        {/* Pop-up nieuwe klant (rechts onderaan) */}
        {showNewClient && (
          <Box sx={{ position: 'fixed', bottom: 24, right: 24, zIndex: 1400 }}>
            <Paper sx={{ p: 2, bgcolor: '#fff', color: '#232323', minWidth: 260, boxShadow: 6, borderRadius: 2, fontWeight: 700, border: '2px solid #E04A39' }}>
              <Typography variant="subtitle1" sx={{ fontWeight: 700, color: '#E04A39' }}>Nieuwe klant!</Typography>
              <Typography variant="body2">Welkom aan <b>{randomClient}</b> als nieuwe klant van Bloxx Connect!</Typography>
            </Paper>
          </Box>
        )}
        <Paper elevation={3} sx={{ p: 4, width: '100%' }}>
          <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', mb: 2 }}>
            <img src="/logo.png" alt="Bloxx Connect Logo" style={{ height: 70, marginBottom: 8 }} />
            <Typography component="h1" variant="h5" align="center" gutterBottom sx={{ color: textColor, fontWeight: 700 }}>
              {/* Leeg, alleen logo */}
            </Typography>
          </Box>
          <Grid container spacing={2} sx={{ mb: 2 }}>
            <Grid item xs={6}>
              <Button
                fullWidth
                variant={loginMethod === 'key' ? 'contained' : 'outlined'}
                onClick={() => setLoginMethod('key')}
                sx={{
                  bgcolor: loginMethod === 'key' ? primaryColor : undefined,
                  color: loginMethod === 'key' ? '#fff' : primaryColor,
                  borderColor: primaryColor,
                  '&:hover': { bgcolor: primaryColor, color: '#fff' },
                }}
              >
                Sleutel
              </Button>
            </Grid>
            <Grid item xs={6}>
              <Button
                fullWidth
                variant={loginMethod === 'code' ? 'contained' : 'outlined'}
                onClick={() => setLoginMethod('code')}
                sx={{
                  bgcolor: loginMethod === 'code' ? primaryColor : undefined,
                  color: loginMethod === 'code' ? '#fff' : primaryColor,
                  borderColor: primaryColor,
                  '&:hover': { bgcolor: primaryColor, color: '#fff' },
                }}
              >
                Login Code
              </Button>
            </Grid>
          </Grid>

          <TextField
            margin="normal"
            required
            fullWidth
            label={loginMethod === 'key' ? 'Sleutel' : 'Login Code'}
            value={input}
            onChange={(e) => setInput(e.target.value)}
            InputLabelProps={{ style: { color: textColor } }}
          />

          <Button
            fullWidth
            variant="contained"
            sx={{ mt: 3, mb: 2, bgcolor: primaryColor, color: '#fff', '&:hover': { bgcolor: '#c13d2e' } }}
            onClick={handleLogin}
          >
            Inloggen
          </Button>

          <Button
            fullWidth
            variant="outlined"
            onClick={() => navigate('/portal')}
            sx={{ color: primaryColor, borderColor: primaryColor, '&:hover': { bgcolor: primaryColor, color: '#fff' } }}
          >
            Klant worden
          </Button>
          <Typography variant="body2" sx={{ mt: 2, textAlign: 'center' }}>
            <a href="/contact" style={{ color: primaryColor, textDecoration: 'underline' }}>Problemen met inloggen?</a>
          </Typography>
        </Paper>
      </Box>
    </Container>
  );
};

export default LoginPage; 