import React, { useState } from 'react';
import {
  AppBar,
  Box,
  Toolbar,
  Typography,
  Button,
  Container,
  Grid,
  Paper,
  Menu,
  MenuItem,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Snackbar,
  Alert,
  Select,
  FormControl,
  InputLabel,
  Autocomplete,
  Badge
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import FavoriteIcon from '@mui/icons-material/Favorite';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import NotificationsIcon from '@mui/icons-material/Notifications';
import ReceiptLongIcon from '@mui/icons-material/ReceiptLong';
import DescriptionIcon from '@mui/icons-material/Description';
import BusinessIcon from '@mui/icons-material/Business';
import AssignmentIcon from '@mui/icons-material/Assignment';
import ReceiptIcon from '@mui/icons-material/Receipt';
import SupportAgentIcon from '@mui/icons-material/SupportAgent';
import LocalOfferIcon from '@mui/icons-material/LocalOffer';

const mockCompanies = {
  werkAanbieders: [
    { 
      naam: 'Bouwbedrijf Jansen', 
      gemeente: 'Gent', 
      profiel: 'Aannemer', 
      betaling: '12.000', 
      dagen: 15, 
      locatie: 'Gent', 
      project: 'Nieuwbouw school', 
      btw: 'BE0123.111.111',
      onlineDatum: '2024-03-20',
      startDatum: '2024-04-01'
    },
    { 
      naam: 'Technische Dienstverlening BV', 
      gemeente: 'Wachtebeke', 
      profiel: 'Technisch specialist', 
      betaling: '8.500', 
      dagen: 10, 
      locatie: 'Wachtebeke', 
      project: 'Renovatie sporthal', 
      btw: 'BE0123.222.222',
      onlineDatum: '2024-03-19',
      startDatum: '2024-03-25'
    },
    { 
      naam: 'Installatie Service', 
      gemeente: 'Lokeren', 
      profiel: 'Installateur', 
      betaling: '5.000', 
      dagen: 7, 
      locatie: 'Lokeren', 
      project: 'Zonnepanelen installatie', 
      btw: 'BE0123.333.333',
      onlineDatum: '2024-03-18',
      startDatum: '2024-03-22'
    },
    { 
      naam: 'Constructie Groep', 
      gemeente: 'Sint-Niklaas', 
      profiel: 'Bouwgroep', 
      betaling: '20.000', 
      dagen: 20, 
      locatie: 'Sint-Niklaas', 
      project: 'Bedrijfshal uitbreiding', 
      btw: 'BE0123.444.444',
      onlineDatum: '2024-03-17',
      startDatum: '2024-03-20'
    },
  ],
  werkGezocht: [
    { naam: 'Project Management BV', gemeente: 'Antwerpen', profiel: 'Projectmanager', betaling: '3.000', dagen: 5, locatie: 'Antwerpen', project: 'Projectcordinatie', btw: 'BE0123.555.555' },
    { naam: 'Adviesbureau Innovatie', gemeente: 'Brugge', profiel: 'Adviseur', betaling: '2.500', dagen: 4, locatie: 'Brugge', project: 'Innovatieadvies', btw: 'BE0123.666.666' },
    { naam: 'Consultancy Partners', gemeente: 'Gent', profiel: 'Consultant', betaling: '4.000', dagen: 6, locatie: 'Gent', project: 'Procesoptimalisatie', btw: 'BE0123.777.777' },
    { naam: 'Strategisch Advies', gemeente: 'Wachtebeke', profiel: 'Strategisch adviseur', betaling: '3.500', dagen: 5, locatie: 'Wachtebeke', project: 'Strategisch plan', btw: 'BE0123.888.888' },
  ],
  contacten: [
    { naam: 'Jan de Vries', gemeente: 'Gent', profiel: 'Contactpersoon', betaling: '-', dagen: '-', locatie: 'Gent', project: '-', btw: 'BE0123.999.999' },
    { naam: 'Maria Jansen', gemeente: 'Wachtebeke', profiel: 'Contactpersoon', betaling: '-', dagen: '-', locatie: 'Wachtebeke', project: '-', btw: 'BE0123.000.000' },
    { naam: 'Peter Smit', gemeente: 'Lokeren', profiel: 'Contactpersoon', betaling: '-', dagen: '-', locatie: 'Lokeren', project: '-', btw: 'BE0123.123.123' },
    { naam: 'Lisa Bakker', gemeente: 'Sint-Niklaas', profiel: 'Contactpersoon', betaling: '-', dagen: '-', locatie: 'Sint-Niklaas', project: '-', btw: 'BE0123.321.321' },
  ],
};

const sectoren = ['Alle sectoren', 'Dakwerker', 'Schrijnwerker', 'Loodgieter', 'Elektricien', 'Metselaar', 'Adviseur', 'Consultant', 'Projectmanager'];

const bijberoepKlanten = [
  { naam: 'Tom Jacobs', beroep: 'Schrijnwerker', regio: 'Gent', profiel: 'Schrijnwerker', project: 'Kleine renovaties', betaling: '1.200', dagen: 3, locatie: 'Gent', btw: 'BE0123.111.222' },
  { naam: 'Els Peeters', beroep: 'Elektricien', regio: 'Antwerpen', profiel: 'Elektricien', project: 'Particuliere klussen', betaling: '900', dagen: 2, locatie: 'Antwerpen', btw: 'BE0123.333.444' },
  { naam: 'Samira El Amrani', beroep: 'Loodgieter', regio: 'Brugge', profiel: 'Loodgieter', project: 'Badkamer herstellingen', betaling: '1.500', dagen: 4, locatie: 'Brugge', btw: 'BE0123.555.666' }
];

const actieveDeals = [
  { titel: 'Dakrenovatie Gent', klant: 'Bouwbedrijf Jansen', status: 'Lopend' },
  { titel: 'Installatie zonnepanelen', klant: 'Installatie Service', status: 'In uitvoering' },
  { titel: 'Adviestraject Brugge', klant: 'Adviesbureau Innovatie', status: 'Nieuw' }
];

// Mock data voor banner
const bannerItems = [
  'Nieuw: Van den Broeck Bouw uit Gent is nu actief op Bloxx!',
  'Deal gesloten: IT Solutions Gent & De Smet Advies',
  'Nieuw: Bakkerij Van Damme uit Brugge is nu actief!',
  'Deal gesloten: Eco Installaties & Klusbedrijf Peeters',
  'Nieuw: Logistiek Wachtebeke uit Wachtebeke is nu actief!',
  'Deal gesloten: Groep Janssens & Adviesbureau Innovatie'
];

const PortalPage: React.FC = () => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const [addMenuAnchor, setAddMenuAnchor] = useState<null | HTMLElement>(null);
  const [selected, setSelected] = useState<any>(null);
  const [infoOpen, setInfoOpen] = useState(false);
  const [contractOpen, setContractOpen] = useState(false);
  const [signatureA, setSignatureA] = useState('');
  const [signatureB, setSignatureB] = useState('');
  const [signedA, setSignedA] = useState(false);
  const [signedB, setSignedB] = useState(false);
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [page, setPage] = useState('portal');
  const [addType, setAddType] = useState<string | null>(null);
  const [addOpen, setAddOpen] = useState(false);
  const [form, setForm] = useState({ wie: '', wat: '', waar: '', wanneer: '', hoelang: '', prijs: '', regio: '', btw: '' });
  const [showNewClient, setShowNewClient] = useState(true);
  const [showNewProvider, setShowNewProvider] = useState(true);
  const [showNewSeeker, setShowNewSeeker] = useState(true);
  const [filters, setFilters] = useState({
    sector: 'Alle sectoren',
    regio: 'Alle regio\'s',
    minPrijs: '',
    maxPrijs: ''
  });
  const [searchValue, setSearchValue] = useState<string | null>(null);
  const newClients = [
    'Van den Broeck Bouw',
    'IT Solutions Gent',
    'De Smet Advies',
    'Bakkerij Van Damme',
    'Groep Janssens',
    'Eco Installaties',
    'Logistiek Wachtebeke',
    'Klusbedrijf Peeters',
  ];
  const [randomClient, setRandomClient] = useState(newClients[Math.floor(Math.random() * newClients.length)]);
  const [sector, setSector] = useState('Alle sectoren');
  const [sortByDate, setSortByDate] = useState(false);
  const [favorites, setFavorites] = useState<string[]>([]);
  const [notifications, setNotifications] = useState([
    { id: 1, message: 'Nieuwe match: Bouwbedrijf Jansen past bij jouw zoekopdracht', read: false },
    { id: 2, message: 'Technische Dienstverlening BV heeft gereageerd op jouw aanbod', read: false }
  ]);
  const [notificationAnchor, setNotificationAnchor] = useState<null | HTMLElement>(null);
  const [contractDialogOpen, setContractDialogOpen] = useState(false);
  const [betalingenDialogOpen, setBetalingenDialogOpen] = useState(false);
  const [menuDialog, setMenuDialog] = useState<string | null>(null);
  const [ticketDialogOpen, setTicketDialogOpen] = useState(false);
  const [ticketForm, setTicketForm] = useState({ naam: '', email: '', bericht: '' });
  const [ticketSent, setTicketSent] = useState(false);

  const handleMenuClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleAddClick = (event: React.MouseEvent<HTMLElement>) => {
    setAddMenuAnchor(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
    setAddMenuAnchor(null);
  };

  const handleInfoOpen = (item: any) => {
    setSelected(item);
    setInfoOpen(true);
  };

  const handleInfoClose = () => {
    setInfoOpen(false);
    setSelected(null);
  };

  const handleContractOpen = () => {
    setContractOpen(true);
    setInfoOpen(false);
  };

  const handleContractClose = () => {
    setContractOpen(false);
    setSignatureA('');
    setSignatureB('');
    setSignedA(false);
    setSignedB(false);
  };

  const handleSignA = () => {
    if (signatureA.trim().toLowerCase() === 'akkoord') {
      setSignedA(true);
      setSignatureA('');
    }
  };
  const handleSignB = () => {
    if (signatureB.trim().toLowerCase() === 'akkoord') {
      setSignedB(true);
      setSignatureB('');
    }
  };

  React.useEffect(() => {
    if (signedA && signedB) {
      setSnackbarOpen(true);
      setTimeout(() => {
        setContractOpen(false);
        setSignedA(false);
        setSignedB(false);
      }, 2500);
    }
  }, [signedA, signedB]);

  React.useEffect(() => {
    if (showNewClient) {
      const timer = setTimeout(() => {
        setShowNewClient(false);
      }, 6000);
      return () => clearTimeout(timer);
    }
  }, [showNewClient]);

  React.useEffect(() => {
    if (showNewProvider) {
      const timer = setTimeout(() => {
        setShowNewProvider(false);
      }, 6000);
      return () => clearTimeout(timer);
    }
  }, [showNewProvider]);

  React.useEffect(() => {
    if (showNewSeeker) {
      const timer = setTimeout(() => {
        setShowNewSeeker(false);
      }, 6000);
      return () => clearTimeout(timer);
    }
  }, [showNewSeeker]);

  const handleAddType = (type: string) => {
    setAddType(type);
    setAddMenuAnchor(null);
    setAddOpen(true);
  };

  const handleFormChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleFormSubmit = () => {
    if (addType === 'Werk aanbieder') {
      mockCompanies.werkAanbieders.push({ naam: form.wie, gemeente: form.regio, profiel: form.wat, betaling: form.prijs, dagen: Number(form.hoelang), locatie: form.waar, project: form.wanneer, btw: 'BE0000.000.000', onlineDatum: new Date().toISOString().slice(0, 10), startDatum: new Date().toISOString().slice(0, 10) });
    } else if (addType === 'Werk gezocht') {
      mockCompanies.werkGezocht.push({ naam: form.wie, gemeente: form.regio, profiel: form.wat, betaling: form.prijs, dagen: Number(form.hoelang), locatie: form.waar, project: form.wanneer, btw: 'BE0000.000.000' });
    }
    setAddOpen(false);
    setForm({ wie: '', wat: '', waar: '', wanneer: '', hoelang: '', prijs: '', regio: '', btw: '' });
    setAddType(null);
  };

  const menuItems = [
    { label: 'MyBloxx', page: 'mybloxx' },
    { label: 'Mijn contracten', page: 'contracten' },
    { label: 'Facturatie', page: 'facturatie' },
    { label: 'Klantenservice', page: 'klantenservice' },
    { label: 'Deals', page: 'deals' },
  ];

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('nl-BE', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric'
    });
  };

  const sortedWerkAanbieders = [...mockCompanies.werkAanbieders].sort((a, b) => {
    if (sortByDate) {
      return new Date(b.onlineDatum).getTime() - new Date(a.onlineDatum).getTime();
    }
    return 0;
  });

  const handleFavoriteClick = (naam: string) => {
    setFavorites(prev => 
      prev.includes(naam) 
        ? prev.filter(f => f !== naam)
        : [...prev, naam]
    );
  };

  const handleNotificationClick = (event: React.MouseEvent<HTMLElement>) => {
    setNotificationAnchor(event.currentTarget);
  };

  const handleNotificationClose = () => {
    setNotificationAnchor(null);
  };

  const markNotificationAsRead = (id: number) => {
    setNotifications(prev =>
      prev.map(notification =>
        notification.id === id
          ? { ...notification, read: true }
          : notification
      )
    );
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            <img src="/logo.png" alt="Logo" style={{ height: '40px' }} />
          </Typography>
          
          <Box sx={{ width: 300, mr: 2 }}>
            <Autocomplete
              value={searchValue}
              onChange={(event, newValue) => {
                setSearchValue(newValue);
                if (newValue) {
                  setFilters({ ...filters, sector: newValue });
                }
              }}
              options={sectoren}
              renderInput={(params) => (
                <TextField
                  {...params}
                  placeholder="Zoek op sector..."
                  size="small"
                  sx={{
                    bgcolor: '#fff',
                    borderRadius: 1,
                    '& .MuiOutlinedInput-root': {
                      '& fieldset': {
                        borderColor: 'transparent',
                      },
                      '&:hover fieldset': {
                        borderColor: 'transparent',
                      },
                      '&.Mui-focused fieldset': {
                        borderColor: '#E04A39',
                      },
                    },
                  }}
                />
              )}
              renderOption={(props, option) => (
                <li {...props}>
                  <Box sx={{ display: 'flex', alignItems: 'center' }}>
                    <SearchIcon sx={{ mr: 1, color: '#E04A39' }} />
                    {option}
                  </Box>
                </li>
              )}
            />
          </Box>

          <IconButton 
            color="inherit" 
            onClick={handleNotificationClick}
            sx={{ mr: 2 }}
          >
            <Badge badgeContent={notifications.filter(n => !n.read).length} color="error">
              <NotificationsIcon />
            </Badge>
          </IconButton>

          <Menu
            anchorEl={notificationAnchor}
            open={Boolean(notificationAnchor)}
            onClose={handleNotificationClose}
            PaperProps={{
              sx: { width: 320, maxHeight: 400 }
            }}
          >
            {notifications.length === 0 ? (
              <MenuItem disabled>Geen nieuwe notificaties</MenuItem>
            ) : (
              notifications.map((notification) => (
                <MenuItem 
                  key={notification.id}
                  onClick={() => markNotificationAsRead(notification.id)}
                  sx={{
                    bgcolor: notification.read ? 'inherit' : 'action.hover',
                    whiteSpace: 'normal',
                    py: 1
                  }}
                >
                  <Typography variant="body2">{notification.message}</Typography>
                </MenuItem>
              ))
            )}
          </Menu>

          <Button color="inherit" onClick={handleMenuClick}>
            <MenuIcon />
          </Button>
          
          <Button
            color="inherit"
            startIcon={<AddIcon />}
            onClick={handleAddClick}
          >
            Toevoegen
          </Button>
        </Toolbar>
      </AppBar>

      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        {menuItems.map((item) => (
          <MenuItem key={item.page} onClick={() => { setPage(item.page); handleClose(); }}>
            {item.label}
          </MenuItem>
        ))}
      </Menu>

      <Menu
        anchorEl={addMenuAnchor}
        open={Boolean(addMenuAnchor)}
        onClose={handleClose}
      >
        <MenuItem onClick={() => handleAddType('Werk aanbieder')}>Werk aanbieder</MenuItem>
        <MenuItem onClick={() => handleAddType('Werk gezocht')}>Werk gezocht</MenuItem>
      </Menu>

      {/* Horizontale balk met menu-knoppen */}
      <Box sx={{ width: '100%', bgcolor: '#f5f5f5', py: 1, mb: 2, display: 'flex', justifyContent: 'center', gap: 2, boxShadow: 1 }}>
        <Button startIcon={<BusinessIcon />} onClick={() => setMenuDialog('mybloxx')} variant="outlined">MyBloxx</Button>
        <Button startIcon={<AssignmentIcon />} onClick={() => setMenuDialog('contracten')} variant="outlined">Mijn contracten</Button>
        <Button startIcon={<ReceiptIcon />} onClick={() => setMenuDialog('facturatie')} variant="outlined">Facturatie</Button>
        <Button startIcon={<SupportAgentIcon />} onClick={() => setMenuDialog('klantenservice')} variant="outlined">Klantenservice</Button>
        <Button startIcon={<LocalOfferIcon />} onClick={() => setMenuDialog('deals')} variant="outlined">Deals</Button>
      </Box>

      {/* Paginaweergave */}
      {page === 'portal' && (
        <Container maxWidth="lg" sx={{ mt: 4, mb: 8 }}>
          <Grid container spacing={3}>
            {/* Bovenste rij */}
            <Grid item xs={12} md={4}>
              {/* Verfijn zoekresultaat */}
              <Paper sx={{ p: 2, bgcolor: '#f5f5f5', borderRadius: 2, position: 'sticky', top: 20 }}>
                <Typography variant="h6" gutterBottom sx={{ borderBottom: '2px solid #E04A39', pb: 1, mb: 2, fontWeight: 700 }}>
                  Verfijn zoekresultaat
                </Typography>
                
                <Box sx={{ mb: 3 }}>
                  <Typography variant="subtitle2" sx={{ mb: 1, fontWeight: 600 }}>Sector</Typography>
                  <FormControl fullWidth size="small">
                    <Select
                      value={filters.sector}
                      onChange={(e) => setFilters({ ...filters, sector: e.target.value })}
                      sx={{ bgcolor: '#fff' }}
                    >
                      {sectoren.map((sector) => (
                        <MenuItem key={sector} value={sector}>{sector}</MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Box>

                <Box sx={{ mb: 3 }}>
                  <Typography variant="subtitle2" sx={{ mb: 1, fontWeight: 600 }}>Regio</Typography>
                  <FormControl fullWidth size="small">
                    <Select
                      value={filters.regio}
                      onChange={(e) => setFilters({ ...filters, regio: e.target.value })}
                      sx={{ bgcolor: '#fff' }}
                    >
                      <MenuItem value="Alle regio's">Alle regio's</MenuItem>
                      <MenuItem value="Gent">Gent</MenuItem>
                      <MenuItem value="Antwerpen">Antwerpen</MenuItem>
                      <MenuItem value="Brussel">Brussel</MenuItem>
                      <MenuItem value="Brugge">Brugge</MenuItem>
                      <MenuItem value="Leuven">Leuven</MenuItem>
                    </Select>
                  </FormControl>
                </Box>

                <Box sx={{ mb: 3 }}>
                  <Typography variant="subtitle2" sx={{ mb: 1, fontWeight: 600 }}>Prijsrange</Typography>
                  <Grid container spacing={1}>
                    <Grid item xs={6}>
                      <TextField
                        size="small"
                        placeholder="Min "
                        value={filters.minPrijs}
                        onChange={(e) => setFilters({ ...filters, minPrijs: e.target.value })}
                        sx={{ bgcolor: '#fff' }}
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <TextField
                        size="small"
                        placeholder="Max "
                        value={filters.maxPrijs}
                        onChange={(e) => setFilters({ ...filters, maxPrijs: e.target.value })}
                        sx={{ bgcolor: '#fff' }}
                      />
                    </Grid>
                  </Grid>
                </Box>

                <Button
                  variant="contained"
                  fullWidth
                  sx={{
                    bgcolor: '#E04A39',
                    color: '#fff',
                    '&:hover': { bgcolor: '#c13d2e' }
                  }}
                  onClick={() => {
                    // Reset filters
                    setFilters({
                      sector: 'Alle sectoren',
                      regio: 'Alle regio\'s',
                      minPrijs: '',
                      maxPrijs: ''
                    });
                  }}
                >
                  Reset filters
                </Button>
              </Paper>
            </Grid>
            <Grid item xs={12} md={4}>
              {/* Actieve deals */}
              <Paper sx={{ p: 2, bgcolor: '#f5f5f5', borderRadius: 2, minHeight: 180, boxShadow: 1 }}>
                <Typography variant="h6" sx={{ fontWeight: 700, color: '#E04A39', mb: 2 }}>
                  Actieve deals
                </Typography>
                {actieveDeals.map((deal) => (
                  <Box key={deal.titel} sx={{ mb: 1 }}>
                    <Typography variant="body2" sx={{ fontWeight: 600 }}>{deal.titel}</Typography>
                    <Typography variant="caption" sx={{ color: '#888' }}>{deal.klant}  {deal.status}</Typography>
                  </Box>
                ))}
              </Paper>
            </Grid>
            <Grid item xs={12} md={4}>
              {/* Contacten */}
              <Paper sx={{ p: 2, mb: 2, bgcolor: '#f5f5f5', borderRadius: 2 }}>
                <Typography variant="h6" gutterBottom sx={{ borderBottom: '2px solid #E04A39', pb: 1, mb: 2, fontWeight: 700 }}>
                  Contacten
                </Typography>
                {mockCompanies.contacten.map((contact) => (
                  <Paper key={contact.naam} sx={{ p: 2, mb: 1, display: 'flex', justifyContent: 'space-between', alignItems: 'center', borderRadius: 2, boxShadow: 1 }}>
                    <Box>
                      <Typography sx={{ fontWeight: 600 }}>{contact.naam}</Typography>
                      <Typography variant="body2" sx={{ color: '#888' }}>{contact.gemeente}</Typography>
                    </Box>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <IconButton
                        onClick={() => handleFavoriteClick(contact.naam)}
                        sx={{ 
                          color: favorites.includes(contact.naam) ? '#E04A39' : 'inherit',
                          '&:hover': { color: '#E04A39' }
                        }}
                      >
                        {favorites.includes(contact.naam) ? <FavoriteIcon /> : <FavoriteBorderIcon />}
                      </IconButton>
                      <Button 
                        variant="outlined" 
                        size="small" 
                        sx={{ ml: 1, borderColor: '#E04A39', color: '#E04A39', fontWeight: 600 }} 
                        onClick={() => handleInfoOpen(contact)}
                      >
                        Meer info
                      </Button>
                    </Box>
                  </Paper>
                ))}
              </Paper>
            </Grid>
            {/* Onderste rij */}
            <Grid item xs={12} md={4}>
              {/* Werk aanbieders */}
              <Paper sx={{ p: 2, mb: 2, bgcolor: '#f5f5f5', borderRadius: 2 }}>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                  <Typography variant="h6" sx={{ borderBottom: '2px solid #E04A39', pb: 1, fontWeight: 700 }}>
                    Werk aanbieders
                  </Typography>
                  <Button
                    variant="outlined"
                    size="small"
                    onClick={() => setSortByDate(!sortByDate)}
                    sx={{
                      borderColor: sortByDate ? '#E04A39' : 'inherit',
                      color: sortByDate ? '#E04A39' : 'inherit',
                      '&:hover': {
                        borderColor: '#E04A39',
                        color: '#E04A39'
                      }
                    }}
                  >
                    {sortByDate ? 'Meest recent ' : 'Meest recent'}
                  </Button>
                </Box>
                {showNewProvider && (
                  <Paper sx={{ p: 2, mb: 2, bgcolor: '#fff', color: '#232323', boxShadow: 6, borderRadius: 2, fontWeight: 700, border: '2px solid #E04A39' }}>
                    <Typography variant="subtitle1" sx={{ fontWeight: 700, color: '#E04A39' }}>Nieuwe aanbieder!</Typography>
                    <Typography variant="body2">Welkom aan <b>{mockCompanies.werkAanbieders[0].naam}</b> als nieuwe aanbieder!</Typography>
                  </Paper>
                )}
                {sortedWerkAanbieders.map((bedrijf) => (
                  <Paper key={bedrijf.naam} sx={{ p: 2, mb: 1, display: 'flex', justifyContent: 'space-between', alignItems: 'center', borderRadius: 2, boxShadow: 1 }}>
                    <Box>
                      <Typography sx={{ fontWeight: 600 }}>{bedrijf.naam}</Typography>
                      <Typography variant="body2" sx={{ color: '#888' }}>{bedrijf.gemeente}</Typography>
                      <Typography variant="body2" sx={{ color: '#666', fontSize: '0.8rem', mt: 0.5 }}>
                        Online sinds: {formatDate(bedrijf.onlineDatum)} | Start: {formatDate(bedrijf.startDatum)}
                      </Typography>
                    </Box>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <IconButton
                        onClick={() => handleFavoriteClick(bedrijf.naam)}
                        sx={{ 
                          color: favorites.includes(bedrijf.naam) ? '#E04A39' : 'inherit',
                          '&:hover': { color: '#E04A39' }
                        }}
                      >
                        {favorites.includes(bedrijf.naam) ? <FavoriteIcon /> : <FavoriteBorderIcon />}
                      </IconButton>
                      <Button 
                        variant="outlined" 
                        size="small" 
                        sx={{ ml: 1, borderColor: '#E04A39', color: '#E04A39', fontWeight: 600 }} 
                        onClick={() => handleInfoOpen(bedrijf)}
                      >
                        Meer info
                      </Button>
                    </Box>
                  </Paper>
                ))}
              </Paper>
            </Grid>
            <Grid item xs={12} md={4}>
              {/* Werk gezocht */}
              <Paper sx={{ p: 2, mb: 2, bgcolor: '#f5f5f5', borderRadius: 2 }}>
                <Typography variant="h6" gutterBottom sx={{ borderBottom: '2px solid #E04A39', pb: 1, mb: 2, fontWeight: 700 }}>
                  Werk gezocht
                </Typography>
                {showNewSeeker && (
                  <Paper sx={{ p: 2, mb: 2, bgcolor: '#fff', color: '#232323', boxShadow: 6, borderRadius: 2, fontWeight: 700, border: '2px solid #E04A39' }}>
                    <Typography variant="subtitle1" sx={{ fontWeight: 700, color: '#E04A39' }}>Nieuwe zoeker!</Typography>
                    <Typography variant="body2">Welkom aan <b>{mockCompanies.werkGezocht[0].naam}</b> als nieuwe zoeker!</Typography>
                  </Paper>
                )}
                {mockCompanies.werkGezocht.map((bedrijf) => (
                  <Paper key={bedrijf.naam} sx={{ p: 2, mb: 1, display: 'flex', justifyContent: 'space-between', alignItems: 'center', borderRadius: 2, boxShadow: 1 }}>
                    <Box>
                      <Typography sx={{ fontWeight: 600 }}>{bedrijf.naam}</Typography>
                      <Typography variant="body2" sx={{ color: '#888' }}>{bedrijf.gemeente}</Typography>
                    </Box>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <IconButton
                        onClick={() => handleFavoriteClick(bedrijf.naam)}
                        sx={{ 
                          color: favorites.includes(bedrijf.naam) ? '#E04A39' : 'inherit',
                          '&:hover': { color: '#E04A39' }
                        }}
                      >
                        {favorites.includes(bedrijf.naam) ? <FavoriteIcon /> : <FavoriteBorderIcon />}
                      </IconButton>
                      <Button 
                        variant="outlined" 
                        size="small" 
                        sx={{ ml: 1, borderColor: '#E04A39', color: '#E04A39', fontWeight: 600 }} 
                        onClick={() => handleInfoOpen(bedrijf)}
                      >
                        Meer info
                      </Button>
                    </Box>
                  </Paper>
                ))}
              </Paper>
            </Grid>
            <Grid item xs={12} md={4}>
              {/* Bijberoep */}
              <Paper sx={{ p: 2, mb: 2, bgcolor: '#f5f5f5', borderRadius: 2 }}>
                <Typography variant="h6" gutterBottom sx={{ borderBottom: '2px solid #E04A39', pb: 1, mb: 2, fontWeight: 700 }}>
                  Bijberoep
                </Typography>
                {bijberoepKlanten.map((klant) => (
                  <Paper key={klant.naam} sx={{ p: 2, mb: 1, display: 'flex', justifyContent: 'space-between', alignItems: 'center', borderRadius: 2, boxShadow: 1 }}>
                    <Box>
                      <Typography sx={{ fontWeight: 600 }}>{klant.naam}</Typography>
                      <Typography variant="body2" sx={{ color: '#888' }}>{klant.regio}</Typography>
                    </Box>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <IconButton
                        onClick={() => handleFavoriteClick(klant.naam)}
                        sx={{ 
                          color: favorites.includes(klant.naam) ? '#E04A39' : 'inherit',
                          '&:hover': { color: '#E04A39' }
                        }}
                      >
                        {favorites.includes(klant.naam) ? <FavoriteIcon /> : <FavoriteBorderIcon />}
                      </IconButton>
                      <Button 
                        variant="outlined" 
                        size="small" 
                        sx={{ ml: 1, borderColor: '#E04A39', color: '#E04A39', fontWeight: 600 }} 
                        onClick={() => handleInfoOpen(klant)}
                      >
                        Meer info
                      </Button>
                    </Box>
                  </Paper>
                ))}
              </Paper>
            </Grid>
          </Grid>
        </Container>
      )}
      {page === 'mybloxx' && (
        <Container maxWidth="md" sx={{ mt: 8 }}><Paper sx={{ p: 4, textAlign: 'center' }}><Typography variant="h5">Welkom bij MyBloxx</Typography></Paper></Container>
      )}
      {page === 'contracten' && (
        <Container maxWidth="md" sx={{ mt: 8 }}><Paper sx={{ p: 4, textAlign: 'center' }}><Typography variant="h5">Mijn contracten</Typography></Paper></Container>
      )}
      {page === 'facturatie' && (
        <Container maxWidth="md" sx={{ mt: 8 }}><Paper sx={{ p: 4, textAlign: 'center' }}><Typography variant="h5">Facturatie</Typography></Paper></Container>
      )}
      {page === 'klantenservice' && (
        <Container maxWidth="md" sx={{ mt: 8 }}><Paper sx={{ p: 4, textAlign: 'center' }}><Typography variant="h5">Klantenservice</Typography></Paper></Container>
      )}
      {page === 'deals' && (
        <Container maxWidth="md" sx={{ mt: 8 }}><Paper sx={{ p: 4, textAlign: 'center' }}><Typography variant="h5">Deals</Typography></Paper></Container>
      )}
      {/* Info dialoog */}
      <Dialog open={infoOpen} onClose={handleInfoClose} maxWidth="sm" fullWidth>
        <DialogTitle>Profielinformatie</DialogTitle>
        <DialogContent>
          {selected && (
            <Box>
              <Typography><b>Naam:</b> {selected.naam}</Typography>
              <Typography><b>Type profiel:</b> {selected.profiel}</Typography>
              <Typography><b>Project:</b> {selected.project}</Typography>
              <Typography><b>Betaling:</b> {selected.betaling}</Typography>
              <Typography><b>Aantal dagen:</b> {selected.dagen}</Typography>
              <Typography><b>Locatie:</b> {selected.locatie}</Typography>
              <Box sx={{ mt: 2, display: 'flex', gap: 2 }}>
                <Button variant="contained" color="primary" onClick={() => window.open(`https://www.google.com/maps/dir/?api=1&destination=${encodeURIComponent(selected.locatie)}`)}>
                  Route
                </Button>
                <Button variant="contained" color="secondary" onClick={handleContractOpen}>
                  Samenwerken
                </Button>
              </Box>
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleInfoClose}>Sluiten</Button>
        </DialogActions>
      </Dialog>
      {/* Contract dialoog */}
      <Dialog open={contractOpen} onClose={handleContractClose} maxWidth="md" fullWidth>
        <DialogTitle>Overeenkomst</DialogTitle>
        <DialogContent>
          {selected && (
            <Box>
              <Typography variant="h6" sx={{ mb: 2 }}>Project: {selected.project}</Typography>
              <Typography><b>Werkaanbieder:</b> {selected.naam} ({selected.btw})</Typography>
              <Typography><b>Opdrachtgever:</b> Uw Bedrijf (BE0000.000.000)</Typography>
              <Typography><b>Locatie:</b> {selected.locatie}</Typography>
              <Typography><b>Afgesproken vergoeding:</b> {selected.betaling}</Typography>
              <Typography><b>Kosten Bloxx Deal:</b> 99,00 excl. btw (voor werkaanbieder)</Typography>
              <Typography sx={{ mt: 2, mb: 1 }}><b>Algemene voorwaarden:</b></Typography>
              <Typography variant="body2" sx={{ mb: 2 }}>
                Deze overeenkomst is bindend na digitale ondertekening door beide partijen. De kosten van Bloxx Deal worden automatisch gefactureerd aan de werkaanbieder. Zie www.bloxx-connect.be/voorwaarden voor details.
              </Typography>
              <Grid container spacing={2}>
                <Grid item xs={12} md={6}>
                  <Typography variant="subtitle2">Werkaanbieder ondertekent:</Typography>
                  {signedA ? (
                    <Alert severity="success">Handtekening geplaatst</Alert>
                  ) : (
                    <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
                      <TextField
                        label="Type 'akkoord'"
                        value={signatureA}
                        onChange={e => setSignatureA(e.target.value)}
                        size="small"
                      />
                      <Button variant="contained" onClick={handleSignA} disabled={signedA || signatureA.trim().toLowerCase() !== 'akkoord'}>
                        Ondertekenen
                      </Button>
                    </Box>
                  )}
                </Grid>
                <Grid item xs={12} md={6}>
                  <Typography variant="subtitle2">Opdrachtgever ondertekent:</Typography>
                  {signedB ? (
                    <Alert severity="success">Handtekening geplaatst</Alert>
                  ) : (
                    <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
                      <TextField
                        label="Type 'akkoord'"
                        value={signatureB}
                        onChange={e => setSignatureB(e.target.value)}
                        size="small"
                      />
                      <Button variant="contained" onClick={handleSignB} disabled={signedB || signatureB.trim().toLowerCase() !== 'akkoord'}>
                        Ondertekenen
                      </Button>
                    </Box>
                  )}
                </Grid>
              </Grid>
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleContractClose}>Sluiten</Button>
        </DialogActions>
      </Dialog>
      {/* Snackbar voor factuurmelding */}
      <Snackbar open={snackbarOpen} autoHideDuration={2500} onClose={() => setSnackbarOpen(false)}>
        <Alert severity="success" sx={{ width: '100%' }}>
          Beide partijen hebben ondertekend. Factuur wordt automatisch aangemaakt door Bloxx.
        </Alert>
      </Snackbar>
      {/* Toevoegen formulier dialoog */}
      <Dialog open={addOpen} onClose={() => setAddOpen(false)} maxWidth="sm" fullWidth>
        <DialogTitle>Voeg {addType} toe</DialogTitle>
        <DialogContent>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, mt: 1 }}>
            <TextField label="Wie (naam)" name="wie" value={form.wie} onChange={handleFormChange} required fullWidth />
            <TextField label="Wat (omschrijving)" name="wat" value={form.wat} onChange={handleFormChange} required fullWidth />
            <TextField label="Waar (locatie)" name="waar" value={form.waar} onChange={handleFormChange} required fullWidth />
            <TextField label="Wanneer (datum of periode)" name="wanneer" value={form.wanneer} onChange={handleFormChange} required fullWidth />
            <TextField label="Hoe lang (aantal dagen)" name="hoelang" value={form.hoelang} onChange={handleFormChange} required fullWidth />
            <TextField label="Prijs" name="prijs" value={form.prijs} onChange={handleFormChange} required fullWidth />
            <TextField label="Regio (gemeente)" name="regio" value={form.regio} onChange={handleFormChange} required fullWidth />
            <TextField label="BTW-nummer" name="btw" value={form.btw || ''} onChange={async (e) => {
              setForm({ ...form, btw: e.target.value });
              const btw = e.target.value.replace(/[^0-9]/g, '');
              if (btw.length === 10) {
                // VIES API call (voorbeeld, werkt alleen met echte API-key of serverproxy)
                try {
                  const response = await fetch(`https://api.vatcomply.com/vat?vat_number=BE${btw}`);
                  const data = await response.json();
                  if (data.valid) {
                    setForm(f => ({ ...f, wie: data.company_name || f.wie, waar: data.address || f.waar }));
                  }
                } catch {}
              }
            }} required fullWidth sx={{ mt: 2 }} />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setAddOpen(false)}>Annuleren</Button>
          <Button variant="contained" onClick={handleFormSubmit}>Toevoegen</Button>
        </DialogActions>
      </Dialog>
      {/* Banner */}
      <Box sx={{
        width: '100%',
        overflow: 'hidden',
        bgcolor: '#E04A39',
        color: '#fff',
        py: 1,
        position: 'fixed',
        left: 0,
        bottom: 0,
        zIndex: 1200
      }}>
        <Box
          component="div"
          sx={{
            display: 'inline-block',
            whiteSpace: 'nowrap',
            animation: 'marquee 30s linear infinite',
            fontWeight: 600,
            fontSize: 16,
            px: 2
          }}
        >
          {bannerItems.map((item, idx) => (
            <span key={idx} style={{ marginRight: 48 }}>{item}</span>
          ))}
        </Box>
        <style>{`
          @keyframes marquee {
            0% { transform: translateX(100%); }
            100% { transform: translateX(-100%); }
          }
        `}</style>
      </Box>
      {/* Floating contract- en betalingen-iconen rechtsonder */}
      <Box sx={{
        position: 'fixed',
        right: 24,
        bottom: 80,
        zIndex: 1300,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'flex-end',
        gap: 2
      }}>
        <IconButton
          sx={{
            bgcolor: '#E04A39',
            color: '#fff',
            boxShadow: 4,
            width: 56,
            height: 56,
            '&:hover': { bgcolor: '#b8372b' }
          }}
          onClick={() => setContractDialogOpen(true)}
        >
          <DescriptionIcon sx={{ fontSize: 32 }} />
        </IconButton>
        <Typography variant="caption" sx={{ color: '#E04A39', fontWeight: 600, mt: 0.5, mb: 1 }}>
          Lopend contract
        </Typography>
        <IconButton
          sx={{
            bgcolor: '#fff',
            color: '#E04A39',
            borderRadius: '50%',
            boxShadow: 3,
            width: 56,
            height: 56,
            '&:hover': { bgcolor: '#fbe9e7' }
          }}
          onClick={() => setBetalingenDialogOpen(true)}
        >
          <ReceiptLongIcon sx={{ fontSize: 32 }} />
        </IconButton>
        <Typography variant="caption" sx={{ color: '#E04A39', fontWeight: 600, mt: 0.5 }}>
          Openstaande betalingen
        </Typography>
      </Box>
      {/* Dialog voor lopend contract */}
      <Dialog open={contractDialogOpen} onClose={() => setContractDialogOpen(false)}>
        <DialogTitle>Lopend contract</DialogTitle>
        <DialogContent>
          <Typography variant="body1" sx={{ mb: 2 }}>
            Je hebt momenteel een actief contract met Bloxx Connect. Bekijk je contractdetails, looptijd en voorwaarden in je account.
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setContractDialogOpen(false)}>Sluiten</Button>
        </DialogActions>
      </Dialog>
      {/* Dialog voor openstaande betalingen */}
      <Dialog open={betalingenDialogOpen} onClose={() => setBetalingenDialogOpen(false)}>
        <DialogTitle>Openstaande betalingen</DialogTitle>
        <DialogContent>
          <Typography variant="body1" sx={{ mb: 2 }}>
            Je hebt nog openstaande betalingen. Ga naar het facturatie-overzicht voor meer details en om te betalen.
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setBetalingenDialogOpen(false)}>Sluiten</Button>
        </DialogActions>
      </Dialog>
      {/* Dialogs voor menu-knoppen */}
      <Dialog open={menuDialog === 'mybloxx'} onClose={() => setMenuDialog(null)}>
        <DialogTitle>MyBloxx</DialogTitle>
        <DialogContent><Typography>Voorbeeld: Overzicht van je profiel, instellingen en statistieken.</Typography></DialogContent>
        <DialogActions><Button onClick={() => setMenuDialog(null)}>Sluiten</Button></DialogActions>
      </Dialog>
      <Dialog open={menuDialog === 'contracten'} onClose={() => setMenuDialog(null)}>
        <DialogTitle>Mijn contracten</DialogTitle>
        <DialogContent><Typography>Voorbeeld: Hier vind je een lijst van al je contracten en hun status.</Typography></DialogContent>
        <DialogActions><Button onClick={() => setMenuDialog(null)}>Sluiten</Button></DialogActions>
      </Dialog>
      <Dialog open={menuDialog === 'facturatie'} onClose={() => setMenuDialog(null)}>
        <DialogTitle>Facturatie</DialogTitle>
        <DialogContent><Typography>Voorbeeld: Bekijk en download je facturen, betaal openstaande bedragen.</Typography></DialogContent>
        <DialogActions><Button onClick={() => setMenuDialog(null)}>Sluiten</Button></DialogActions>
      </Dialog>
      <Dialog open={menuDialog === 'klantenservice'} onClose={() => setMenuDialog(null)}>
        <DialogTitle>Klantenservice</DialogTitle>
        <DialogContent><Typography>Voorbeeld: Neem contact op met support of bekijk veelgestelde vragen.</Typography></DialogContent>
        <DialogActions><Button onClick={() => setMenuDialog(null)}>Sluiten</Button></DialogActions>
      </Dialog>
      <Dialog open={menuDialog === 'deals'} onClose={() => setMenuDialog(null)}>
        <DialogTitle>Deals</DialogTitle>
        <DialogContent><Typography>Voorbeeld: Overzicht van je actieve en historische deals.</Typography></DialogContent>
        <DialogActions><Button onClick={() => setMenuDialog(null)}>Sluiten</Button></DialogActions>
      </Dialog>
      {/* Onderste brede balk met ticket-optie */}
      <Box sx={{
        width: '100%',
        bgcolor: '#f5f5f5',
        color: '#232323',
        py: 3,
        px: { xs: 2, md: 6 },
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        mt: 8,
        mb: 10,
        fontSize: 18,
        fontWeight: 600,
        borderTop: '6px solid #e0e0e0',
        borderBottom: '6px solid #e0e0e0',
        flexWrap: 'wrap',
        gap: 2,
        position: 'relative',
        zIndex: 1100
      }}>
        <Box sx={{ flex: 1, minWidth: 220, borderRight: '4px solid #E04A39', pr: 3, fontSize: 22, fontWeight: 700 }}>
          Heb je nog vragen of opmerkingen?
        </Box>
        <Box sx={{ flex: 2, minWidth: 260, display: 'flex', alignItems: 'center', justifyContent: 'flex-end', gap: 2 }}>
          <span style={{ fontWeight: 500, fontSize: 17, marginRight: 16 }}>
            Zijn er nog vragen of profielen waar je niet meteen mee kan matchen? Wil je dat wij voor jou zoeken naar een specifieke niche om extra klanten toe te voegen? Laat een ticket achter hieronder.
          </span>
          <Button variant="contained" color="secondary" sx={{ fontWeight: 700, fontSize: 18, px: 4, py: 1.5, borderRadius: 3, boxShadow: 2, mr: { xs: 0, md: 12 } }} onClick={() => setTicketDialogOpen(true)}>
            Ticket
          </Button>
        </Box>
      </Box>
      {/* Ticket dialog */}
      <Dialog open={ticketDialogOpen} onClose={() => { setTicketDialogOpen(false); setTicketSent(false); }}>
        <DialogTitle>Ticket aanmaken</DialogTitle>
        <DialogContent>
          {ticketSent ? (
            <Typography variant="h6" sx={{ color: 'green', mb: 2 }}>Bedankt! Je ticket is ingediend. We nemen zo snel mogelijk contact op.</Typography>
          ) : (
            <Box component="form" sx={{ display: 'flex', flexDirection: 'column', gap: 2, mt: 1 }} onSubmit={e => { e.preventDefault(); setTicketSent(true); }}>
              <TextField label="Naam" name="naam" value={ticketForm.naam} onChange={e => setTicketForm({ ...ticketForm, naam: e.target.value })} required fullWidth />
              <TextField label="E-mail" name="email" type="email" value={ticketForm.email} onChange={e => setTicketForm({ ...ticketForm, email: e.target.value })} required fullWidth />
              <TextField label="Bericht" name="bericht" value={ticketForm.bericht} onChange={e => setTicketForm({ ...ticketForm, bericht: e.target.value })} required fullWidth multiline rows={4} />
              <Button type="submit" variant="contained" color="primary">Verzenden</Button>
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={() => { setTicketDialogOpen(false); setTicketSent(false); }}>Sluiten</Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default PortalPage; 