import React, { useState } from 'react';
import { Box, Button, Container, TextField, Typography, Paper } from '@mui/material';

const primaryColor = '#E04A39';
const textColor = '#232323';

const DemoPage: React.FC = () => {
  const [date, setDate] = useState('');
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [submitted, setSubmitted] = useState(false);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setSubmitted(true);
  };

  return (
    <Container maxWidth="sm" sx={{ mt: 8 }}>
      <Paper sx={{ p: 4, textAlign: 'center' }} elevation={4}>
        <Typography variant="h5" sx={{ color: primaryColor, fontWeight: 700, mb: 2 }}>
          Vraag een demo aan
        </Typography>
        <Typography variant="body1" sx={{ color: textColor, mb: 3 }}>
          Kies een datum en vul je gegevens in. Wij nemen contact met je op voor een vrijblijvende demo.
        </Typography>
        {submitted ? (
          <Typography variant="h6" sx={{ color: primaryColor, mt: 4 }}>
            Bedankt voor je aanvraag! We nemen snel contact met je op.
          </Typography>
        ) : (
          <Box component="form" onSubmit={handleSubmit} sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
            <TextField
              label="Naam"
              value={name}
              onChange={e => setName(e.target.value)}
              required
              fullWidth
            />
            <TextField
              label="E-mail"
              type="email"
              value={email}
              onChange={e => setEmail(e.target.value)}
              required
              fullWidth
            />
            <TextField
              label="Kies een datum"
              type="date"
              value={date}
              onChange={e => setDate(e.target.value)}
              required
              fullWidth
              InputLabelProps={{ shrink: true }}
            />
            <Button
              type="submit"
              variant="contained"
              sx={{ bgcolor: primaryColor, color: '#fff', fontWeight: 600, mt: 2, '&:hover': { bgcolor: '#c13d2e' } }}
            >
              Verstuur aanvraag
            </Button>
          </Box>
        )}
      </Paper>
    </Container>
  );
};

export default DemoPage; 