import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import LandingPage from './pages/LandingPage';
import LoginPage from './pages/LoginPage';
import PortalPage from './pages/PortalPage';
import DemoPage from './pages/DemoPage';
import VacaturesPage from './pages/VacaturesPage';
import PrivacyPage from './pages/PrivacyPage';
import VoorwaardenPage from './pages/VoorwaardenPage';
import CookiePage from './pages/CookiePage';
import ContactPage from './pages/ContactPage';

const theme = createTheme({
  palette: {
    primary: {
      main: '#E04A39', // Rood uit het logo
    },
    secondary: {
      main: '#232323', // Donkergrijs uit het logo
    },
    text: {
      primary: '#232323',
    },
  },
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router>
        <Routes>
          <Route path="/" element={<LandingPage />} />
          <Route path="/login" element={<LoginPage />} />
          <Route path="/portal" element={<PortalPage />} />
          <Route path="/demo" element={<DemoPage />} />
          <Route path="/vacatures" element={<VacaturesPage />} />
          <Route path="/privacybeleid" element={<PrivacyPage />} />
          <Route path="/voorwaarden" element={<VoorwaardenPage />} />
          <Route path="/cookiebeleid" element={<CookiePage />} />
          <Route path="/contact" element={<ContactPage />} />
        </Routes>
      </Router>
    </ThemeProvider>
  );
}

export default App; 