import React, { useState } from 'react';
import { Container, Typography, Paper, TextField, Button } from '@mui/material';

const ContactPage: React.FC = () => {
  const [sent, setSent] = useState(false);
  const [form, setForm] = useState({ naam: '', email: '', bericht: '' });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setSent(true);
  };

  return (
    <Container maxWidth="sm" sx={{ mt: 8 }}>
      <Paper sx={{ p: 4, textAlign: 'center' }}>
        <Typography variant="h4" sx={{ mb: 2 }}>Contact</Typography>
        {sent ? (
          <Typography variant="h6" sx={{ color: 'green' }}>Bedankt voor je bericht! We nemen zo snel mogelijk contact op.</Typography>
        ) : (
          <form onSubmit={handleSubmit} style={{ display: 'flex', flexDirection: 'column', gap: 16 }}>
            <TextField label="Naam" name="naam" value={form.naam} onChange={handleChange} required fullWidth />
            <TextField label="E-mail" name="email" type="email" value={form.email} onChange={handleChange} required fullWidth />
            <TextField label="Bericht" name="bericht" value={form.bericht} onChange={handleChange} required fullWidth multiline rows={4} />
            <Button type="submit" variant="contained">Verzenden</Button>
          </form>
        )}
      </Paper>
    </Container>
  );
};

export default ContactPage; 